import InternetPackage from '../utils/InternetPackage';
import TvPackage from '../utils/TvPackage';

export default {
  initialState: {
    currentStep: 1,
    step1Results: null,
    step2Results: [
      {
        "option": "Сърфиране на страници, ползване на е-мейл, месинджъри",
        "isSelected": false
      },
      {
        "option": "Социални мрежи, месинджъри, ползване на облачни услуги",
        "isSelected": false
      },
      {
        "option": "Стриймване и гледане на клипове в YouTube, Vbox7",
        "isSelected": false
      },
      {
        "option": "Споделяне на големи файлове, филми, онлайн игри, гледане на 4к съдържание",
        "isSelected": false
      }
    ],
    step3Results: [
      {
        "option": "1 устройство",
        "isSelected": false
      },
      {
        "option": "2 устройства",
        "isSelected": false
      },
      {
        "option": "3 устройства",
        "isSelected": false
      },
      {
        "option": "4 + устройства",
        "isSelected": false
      }
    ],
    step4Results: [
      {
        "option": "1 телевизор",
        "isSelected": false
      },
      {
        "option": "2 телевизора",
        "isSelected": false
      },
      {
        "option": "3 телевизора",
        "isSelected": false
      }
    ],
    step5Results: [
      {
        "option": "125 канали с вкл. AMC & Cinemax",
        "isSelected": false
      },
      {
        "option": "175 канали с вкл. AMC & Cinemax + HBO",
        "isSelected": false
      },
      {
        "option": "225 канали с вкл. AMC & Cinemax + HBO + Diema Xtra",
        "isSelected": false
      }
    ],
    disableButton: true,
    totalTvSteps: 6,
    totalInternetSteps: 4,
    numberOfSteps: 6,
    progressPercentage: 1,
    goToConfigPackages: false,
    configPackages: [],
    enableNextStep: false,
    tvCount: '',
  },

  reducer: (state, action) => {
    switch (action.type) {
      case 'step_result':
        return {
          ...state,
          enableNextStep: true,
          [action.step_field]: action.step_result,
        }
      case 'update_list_item': {
        const index = action.index;
        let selectedList = state[action.dispatchHandle];
        let newSelectedList = selectedList;
        newSelectedList[index].isSelected = !selectedList[index].isSelected;

        return {
          ...state,
          enableNextStep: true,
          [action.dispatchHandle]: newSelectedList
        }
      }
      case 'update_list_item_single': {
        const index = action.index;
        let selectedList = state[action.dispatchHandle];
        let newSelectedList = selectedList.map(({ option, _ }) => { return { option: option, isSelected: false } });
        newSelectedList[index] = { option: newSelectedList[index].option, isSelected: true };

        return {
          ...state,
          tvCount: action.nullTvCount ? '' : state.tvCount,
          enableNextStep: true,
          [action.dispatchHandle]: newSelectedList,
        }
      }
      case 'tv_value_change': {
        let newSelectedList = state[action.dispatchHandle].map(({ option, isSelected }) => { return { option: option, isSelected: false } });
        return {
          ...state,
          tvCount: action.value,
          enableNextStep: action.value !== '',
          [action.dispatchHandle]: newSelectedList,
        }
      }
      case 'next_step': {
        let tempPercentage = ((state.currentStep + 1) / (state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps)) * 100;
        tempPercentage = Math.round(tempPercentage);
        return {
          ...state,
          currentStep: state.currentStep + 1,
          enableNextStep: false,
          progressPercentage: tempPercentage,
          numberOfSteps: state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps,
        };
      }
      case 'prev_step': {
        let tempPercentage = ((state.currentStep - 1) / (state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps)) * 100;
        tempPercentage = Math.round(tempPercentage);
        return {
          ...state,
          currentStep: state.currentStep - 1,
          disableButton: true,
          progressPercentage: tempPercentage,
          numberOfSteps: state.step1Results === 'ТВ + Интернет' ? state.totalTvSteps : state.totalInternetSteps,
        };
      }
      case 'final_step': {
        return {
          ...state,
          goToConfigPackages: true,
          configPackages: action.configPackages,
        }
      }
      default: {
        return state;
      }
    }
  },

  parseInternetPackage: (s2, s3, allPackages) => {
    const desiredInternetSpeed = getInternetSpeed(s2, s3);
    // sort the packakges by speed
    var filteredPackages = allPackages.filter((node) => node.package === 'Само интернет' && node.duration === 2);
    const relativePackages = getRelativeInternetPackages(desiredInternetSpeed, filteredPackages);

    return [
      new InternetPackage(relativePackages[0].packageid, 'mid', relativePackages[0].speed, relativePackages[0].price, { ...relativePackages[0] }),
      new InternetPackage(relativePackages[1].packageid, 'big', relativePackages[1].speed, relativePackages[1].price, { ...relativePackages[1] }),
      new InternetPackage(relativePackages[2].packageid, 'xl', relativePackages[2].speed, relativePackages[2].price, { ...relativePackages[2] }),
    ]
  },

  parseTvPackage: (s2, s3, s4, s5, allPackages) => {
    const tvCount = extractNumber(getTheLastTrueFromTheList(s4));
    const desiredInternetSpeed = getInternetSpeed(s2, s3);
    const desiredTvPackage = getTheLastTrueFromTheList(s5);

    var filteredPackages = allPackages.filter((node) => node.package === desiredTvPackage && node.duration === 2 && node.tvcount === 1);
    const relativePackages = getRelativeInternetPackages(desiredInternetSpeed, filteredPackages);
    return [
      new TvPackage(relativePackages[0].packageid, 'mid', relativePackages[0].speed, relativePackages[0].price, { ...relativePackages[0] }, relativePackages[0].package, hdFetcherInfo, tvCount),
      new TvPackage(relativePackages[1].packageid, 'big', relativePackages[1].speed, relativePackages[1].price, { ...relativePackages[1] }, relativePackages[1].package, hdFetcherInfo, tvCount),
      new TvPackage(relativePackages[2].packageid, 'xl', relativePackages[2].speed, relativePackages[2].price, { ...relativePackages[2] }, relativePackages[2].package, hdFetcherInfo, tvCount),
    ];
  }
}

const hdFetcherInfo = '';

// Get the relative packages to this speed 
const getRelativeInternetPackages = (desiredInternetSpeed, packages) => {
  // Sort packages by speed
  packages = packages.sort((f, s) => {
    f = parseInt(f.speed.split(' ')[0]);
    s = parseInt(s.speed.split(' ')[0]);
    return f - s;
  });

  var resultPackages = [];

  for (var index = 0; index < packages.length; index++) {
    if (packages[index].speed === desiredInternetSpeed) {
      if (index === packages.length - 1) {
        resultPackages[0] = packages[index - 2];
        resultPackages[1] = packages[index - 1];
        resultPackages[2] = packages[index];
        break;
      } else if (index === 0) {
        resultPackages[0] = packages[index];
        resultPackages[1] = packages[index + 1];
        resultPackages[2] = packages[index + 2];
        break;
      } else {
        resultPackages[0] = packages[index - 1];
        resultPackages[1] = packages[index];
        resultPackages[2] = packages[index + 1];
        break;
      }
    }
  }

  return resultPackages;
}


const getInternetSpeed = (s2, s3) => {
  return calculateInternetSpeed(getTheLastTrueFromTheList(s2), extractNumber(getTheLastTrueFromTheList(s3)));
}

const getTheLastTrueFromTheList = (list) => {
  for (var i = list.length - 1; i >= 0; i--) {
    if (list[i].isSelected) {
      return list[i].option;
    }
  }
  return null;
}

const extractNumber = (string) => {
  return string.trim().split(' ')[0];
}

const calculateInternetSpeed = (internetSpeed, numberOfDevices) => {
  const matrix = [
    'Сърфиране на страници, ползване на е-мейл, месинджъри',
    'Социални мрежи, месинджъри, ползване на облачни услуги',
    'Стриймване и гледане на клипове в YouTube, Vbox7',
    'Споделяне на големи файлове, филми, онлайн игри, гледане на 4к съдържание',
  ];

  switch (internetSpeed) {
    case matrix[0]: {
      const possibleSpeeds = ['85 Mbit/s', '85 Mbit/s', '125 Mbit/s', '125 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
    case matrix[1]: {
      const possibleSpeeds = ['85 Mbit/s', '125 Mbit/s', '125 Mbit/s', '175 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
    case matrix[2]: {
      const possibleSpeeds = ['175 Mbit/s', '225 Mbit/s', '225 Mbit/s', '300 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
    case matrix[3]: {
      const possibleSpeeds = ['175 Mbit/s', '225 Mbit/s', '300 Mbit/s', '300 Mbit/s'];
      return possibleSpeeds[numberOfDevices - 1];
    }
    default: {
      return null;
    }
  }
}